import { Component, OnInit, Input } from '@angular/core';
import { GalleryDescriptionsService } from '../gallery-descriptions.service.js';
import { PagerService } from '../pager.service.js';
import { GalleryFilterPipe } from '../gallery-filter.pipe.js';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html'
})
export class ProductsComponent implements OnInit {
  @Input() compact: boolean = false;
  acType: string = '';
  productList: any;
  pager: any = {};
  pagedItems: any[];

  constructor(
    private galleryDescriptionsService: GalleryDescriptionsService,
    private pagerService: PagerService
  ) { }

  ngOnInit() {
    this.galleryDescriptionsService.getJSON().subscribe(data => {
      this.productList = data;
      this.setPage(1);
    })
  }

  setPage(page: number, filter?: string) {
    let filteredProductList;

    if (page < 1 || page > this.pager.totalPages) {
      return;
    }

    if (filter) {
      filteredProductList = new GalleryFilterPipe().transform(this.productList, filter);
    } else {
      filteredProductList = this.productList;
    }

    this.pager = this.pagerService.getPager(filteredProductList.length, page);
    this.pagedItems = filteredProductList.slice(this.pager.startIndex, this.pager.endIndex + 1);
  }

  filterProductList(filter) {
    this.acType = filter;
    this.setPage(1, filter);
  }
}
