import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'galleryFilter'
})
export class GalleryFilterPipe implements PipeTransform {

  transform(productList: any, acType: string): any[] {
    if (productList) {
        return productList.filter((product: any) => acType ? product.type.indexOf(acType.toLowerCase()) > -1 : true);
    }
  }
}
